.training-home {
    @include adaptiveValue("margin-top", 73, 32, 1296, 576, 1);

    &__wrapper {
        @include adaptiveValue("border-radius", 74, 18, 1296, 576, 1);
        background-image: linear-gradient(196deg, #6BA6FF -44.28%, #1541DF 107.8%);
        padding: 70px 76px 133px;
        
        position: relative;
        &::before{
            content: '';
            position: absolute;
            inset: 0;
            background-image: url('../img/pexels-alex-knight.png');
            background-repeat: no-repeat;
            background-position: right bottom;
            background-size: contain;
            z-index: 0;
            @media (max-width:$tablet-max){
                background-position: right -8% bottom -50px;  
            }
            @media (max-width:$mobile-max){
                background-position: right -50px bottom -15px;  
            }
        }
        @media (max-width:$tablet-max) {
            padding: 56px 32px;
        }
        @media (max-width:$smallTablet-max){
             padding: 32px 24px; 
        }
    }

    &__title {
        color: var(--white);
        @include adaptiveValue("font-size", 32, 16, 1296, 576, 1);
        font-weight: 500;
        line-height: 140%;
        letter-spacing: 0.16px;
        max-width: 660px;

        position: relative;
        z-index: 1;
        @media (max-width:$tablet-max) {
            max-width: none;
        }
    }

    &__tags {        position: relative;
        z-index: 1;
        margin-top: 56px;

        .row {
            display: flex;
            align-items: center;

            &:not(:last-child) {
                margin-bottom: 16px;

                @media (max-width:$mobile-max) {
                    margin-bottom: 8px;
                }
            }

            @media (max-width:$mobile-max) {
                flex-direction: column;
                align-items: start;
            }
        }

        @media (max-width:$tablet-max) {
            margin-top: 32px;
        }
    }

    &__tag {
background-color: #5e85f4;
        &:not(:last-child) {
            margin-right: 16px;

            @media (max-width:$mobile-max) {
                margin-right: 0;
                margin-bottom: 8px;
            }
        }
        &.tag--big{
letter-spacing:normal;
border-radius: 45px;
font-size: 16px;
padding: 0 28px;
height: 58px;
        }
    }
}
